@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap");
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

body {
  font-size: 16px;
  font-family: 'Rubik', sans-serif;
  background-color: #f5f5f5;
  color: #fafafa;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

/*
*   üçç CONTAINER
*/
.container {
  width: 100%;
  max-width: 375px;
  max-height: 812px;
  margin: 0 auto;
  background-color: #262626; }

header {
  height: 100px;
  padding: 1rem;
  background-color: #404040;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  header h1 {
    font-size: 2.5rem;
    font-weight: bold; }

main {
  position: relative;
  height: 612px;
  overflow-y: auto; }
  main .new-clock {
    font-size: 2.5rem;
    font-weight: semi-bold;
    margin-right: 1rem; }
  main h1 {
    letter-spacing: 10px; }

.main-list {
  display: flex;
  flex-direction: column; }
  .main-list li {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #404040;
    padding: 1.5em 1em 1.5em 1em;
    position: relative; }
    .main-list li section {
      align-self: center; }
      .main-list li section .offset-hour {
        font-size: .9rem;
        font-weight: 400;
        margin-bottom: .2em; }
      .main-list li section .city-name {
        font-size: 1.7rem;
        font-weight: bold;
        letter-spacing: .02rem; }
      .main-list li section .time {
        font-size: 3.5rem;
        font-weight: 200; }
  .main-list li:first-child {
    padding-top: .5em; }
  .main-list .delete-btn {
    z-index: 100;
    background-color: red;
    height: 80%;
    width: 70px;
    position: absolute;
    top: 10%;
    left: 0;
    cursor: pointer; }
    .main-list .delete-btn span {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center; }
  .main-list .delete-btn:hover {
    background-color: #ff462d; }

footer {
  height: 100px;
  padding: 1rem;
  background-color: #404040;
  border-top: 1px solid #737373; }

nav ul {
  display: flex;
  justify-content: space-between; }

nav li {
  width: 25%;
  display: flex;
  cursor: pointer; }
  nav li h3 {
    font-size: 0.8rem;
    margin-top: 0.3em; }

nav a {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fafafa;
  font-weight: bold;
  text-decoration: none; }

nav a:hover {
  color: #fdba74; }
  nav a:hover svg {
    color: #fdba74; }

nav .svg-holder {
  width: 36px;
  height: 36px; }

nav svg {
  width: 100%;
  height: 100%; }

nav li:nth-child(3) .svg-holder {
  width: 38px;
  height: 38px; }

a.active,
a:active,
a:focus {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  a.active h3,
  a:active h3,
  a:focus h3 {
    color: #fb923c; }
  a.active svg,
  a:active svg,
  a:focus svg {
    color: #fb923c; }

.edit-new-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding: 0 1em;
  font-size: 1rem; }
  .edit-new-wrapper #edit-clock {
    color: #fb923c;
    cursor: pointer; }
  .edit-new-wrapper #new-clock {
    color: #fb923c;
    font-size: 2rem;
    cursor: pointer; }
  .edit-new-wrapper #edit-clock, .edit-new-wrapper #new-clock:hover {
    color: #fdba74; }

.modal {
  background-color: #404040;
  position: absolute;
  margin: 0 auto;
  top: 10%;
  left: 0;
  right: 0;
  width: 90%;
  display: none;
  z-index: 1000;
  padding: .5em 1em;
  border: 0.25rem solid #737373;
  border-radius: 1rem; }
  .modal .timezone-list {
    display: flex;
    flex-direction: column; }
    .modal .timezone-list li {
      font-size: 1.3rem;
      padding: .5em;
      border-bottom: 1px solid #525252; }
    .modal .timezone-list li:last-child {
      border: 0; }
    .modal .timezone-list li:hover {
      background-color: #525252; }

.active {
  display: block; }

.hidden {
  display: none; }

.main-list li section .offset-hour {
  font-size: .9rem;
  font-weight: 400;
  margin-bottom: .2em; }

.main-list li section .city-name {
  font-size: 1.7rem;
  font-weight: bold;
  letter-spacing: .02rem; }

.main-list li section .time {
  font-size: 3rem;
  font-weight: 200; }

.main-list li section .label {
  font-size: 1.1rem;
  font-weight: 400;
  letter-spacing: .05rem;
  padding-left: .2em; }

.alarmingTextColor {
  color: red; }

.alarmingModal {
  z-index: 100;
  height: 20%;
  border-color: rgba(255, 11, 11, 0.8); }
  .alarmingModal .modal-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    color: red; }

.newAlarmModal {
  padding: 1.5em; }
  .newAlarmModal .row {
    margin: 1em 0;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .newAlarmModal .row input[type="text"], .newAlarmModal .row input[type="time"] {
      width: 60%;
      padding: .4em;
      font-size: 1rem;
      font-family: 'Rubik', sans-serif;
      letter-spacing: .05rem;
      border-radius: .4rem;
      border: 0; }
  .newAlarmModal .modal-footer {
    display: flex;
    justify-content: center; }

input[type="button"] {
  border-radius: 2rem;
  height: 2rem;
  width: 4rem;
  background-color: #e5e5e5;
  border: 3px solid #d4d4d4;
  margin: 0 .5em;
  cursor: pointer; }

input[type="button"]:hover {
  background-color: #fafafa; }

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px; }

.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px; }

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  top: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%; }

input:checked + .slider {
  background-color: #63d863; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

.active {
  display: inline-block; }

button {
  display: none; }
